import { Component } from 'react'
import api from "../../../../lib/api";
import constants from "../../../../lib/constants";
import {formatNumber, setClassNameCondition} from "../../../../lib/helper";

const rankImages = {
	'1': 'data:image/svg+xml;base64,',
	'2': 'data:image/svg+xml;base64,',
	'3': 'data:image/svg+xml;base64,'
};

class Board extends Component{
	constructor(props) {
		super(props);
		this.state = {
			members: [],
			loading_challenge: false
		};
	}

	componentDidMount() {
		this.loadChallengeDetail();
	}

	loadChallengeDetail = () => {
		let { detail } = this.props;
		this.setState({ loading_challenge: true }, () => {
			api.getChallengeBoard(detail).then(({ data }) => {
				this.setState({ members: data, loading_challenge: false });
			});
		})
	}

	render() {
		let { detail } = this.props;
		let { type, status } = detail;
		let { members, loading_challenge } = this.state;
		return (
			<div data-v-2f4ce6a2="" className="leaderBoardBox">
				<div data-v-2f4ce6a2=""
					 className={setClassNameCondition(status === 'live', '', 'hidden', 'd-flex flex-row-reverse')}
					 style={{ paddingTop: '46px' }}>
					<button data-v-2f4ce6a2="" disabled={loading_challenge} onClick={this.loadChallengeDetail} className={setClassNameCondition(loading_challenge, 'loading', '', 'd-flex justify-content-center align-items-center btnRefresh')}>
						<svg data-v-2f4ce6a2=""
							 xmlns="http://www.w3.org/2000/svg"
							 width="16.258" height="15.382"
							 viewBox="0 0 16.258 15.382"
							 className="mr-2">
							<g data-v-2f4ce6a2="" id="undo-29"
							   transform="translate(-0.2 -0.9)">
								<path data-v-2f4ce6a2=""
									  id="Path_34973"
									  data-name="Path 34973"
									  d="M2.64,12.678A7.685,7.685,0,0,1,1.1,9.2l2.167-.364a5.472,5.472,0,0,0,1.1,2.483Z"
									  transform="translate(0.088 0.781)"
									  fill="#fff" />
								<path data-v-2f4ce6a2=""
									  id="Path_34974"
									  data-name="Path 34974"
									  d="M7.127,15.186A7.706,7.706,0,0,1,3.858,13.8l1.3-1.77a5.483,5.483,0,0,0,2.331.986Z"
									  transform="translate(0.36 1.096)"
									  fill="#fff" />
								<path data-v-2f4ce6a2=""
									  id="Path_34975"
									  data-name="Path 34975"
									  d="M9.2,15.192l-.363-2.167a5.481,5.481,0,0,0,2.427-1.059L12.6,13.708A7.686,7.686,0,0,1,9.2,15.192Z"
									  transform="translate(0.85 1.09)"
									  fill="#fff" />
								<path data-v-2f4ce6a2=""
									  id="Path_34976"
									  data-name="Path 34976"
									  d="M8.768,1.01A7.718,7.718,0,0,0,3.333,3.264L.969.9.2,7.93l7.03-.769L4.885,4.816A5.492,5.492,0,0,1,14.261,8.7a5.447,5.447,0,0,1-1.04,3.216L15,13.2A7.691,7.691,0,0,0,8.768,1.01Z"
									  transform="translate(0 0)"
									  fill="#fff"/>
							</g>
						</svg>
						<span data-v-2f4ce6a2=""
							  className="btnText">Cập nhật</span>
					</button>
				</div>
				<div data-v-2f4ce6a2="" className="gridRank leaderboard-talbe">
					<div data-v-2f4ce6a2="" className="cell">
						<div data-v-2f4ce6a2="" className="d-flex gridHeader grid">
							<div data-v-2f4ce6a2="" className="cell first text-center">#</div>
							<div data-v-2f4ce6a2="" className="cell shrink text-capitalize">Biệt danh</div>
							<div data-v-2f4ce6a2="" className="cell auto text-capitalize">{type === constants.CHALLENGE_TYPE.TRADING ? 'Tổng KLGD' : 'Hoa hồng'}</div>
							<div data-v-2f4ce6a2="" className="cell auto text-capitalize">Phần thưởng</div>
						</div>
						{(members || []).map(({ rank, username, value, reward }, index) => {
							return (
								<div key={index} data-v-2f4ce6a2="" className="d-flex gridBody grid">
									<div data-v-2f4ce6a2="" className="cell first text-center">
										{rankImages[rank] ? <img data-v-2f4ce6a2="" src={rankImages[rank]} alt='' width="42" height="42"/> : rank}
									</div>
									<div data-v-2f4ce6a2="" className="cell shrink">
										<span data-v-2f4ce6a2="">{username}</span>
									</div>
									<div data-v-2f4ce6a2="" className="cell auto">${(value)}</div>
									<div data-v-2f4ce6a2="" className="cell auto prize">${formatNumber(reward)}</div>
								</div>
							)
						})}
					</div>
				</div>
			</div>
		)
	}
}

export default Board;
