import { Component } from 'react'
import moment from "moment";
import api from "../../../../lib/api";
import {formatNumber} from "../../../../lib/helper";

class Info extends Component{
	constructor(props) {
		super(props);
		this.state = {
			rewards: [],
			description: ''
		};
	}

	componentDidMount() {
		this.loadChallengeDetail();
	}

	loadChallengeDetail = () => {
		let { detail } = this.props;
		api.getChallengeInfo(detail).then(({ data }) => {
			this.setState({ description: (data.description || '').replace(/\r?\n/g, '<br/>'), rewards: data.rewards });
		});
	}

	render() {
		let { detail } = this.props;
		let { start, end } = detail;
		let { description, rewards } = this.state;
		return (
			<div data-v-2f4ce6a2="" className="infoBox">
				<div data-v-2f4ce6a2="" className="d-flex justify-content-between">
					<div data-v-2f4ce6a2="" className="leftCol"><p data-v-2f4ce6a2="">Thời gian bắt đầu</p> <p
						data-v-2f4ce6a2="">Thời gian kết thúc</p>
					</div>
					<div data-v-2f4ce6a2="" className="rightCol"><p data-v-2f4ce6a2="">{moment(start).format('DD/MM/YYYY HH:mm:ss')}</p> <p
						data-v-2f4ce6a2="" className="mb-0">{moment(end).format('DD/MM/YYYY HH:mm:ss')}</p>
					</div>
				</div>
				<div data-v-2f4ce6a2="">
					<div dangerouslySetInnerHTML={{__html: description}} />
					<p className='mt-3'>*** Thứ hạng và phần thưởng</p>
					<div>
						{(rewards || []).map(({ rank, reward }) => {
							return <p>Hạng {rank}: ${formatNumber(reward)}</p>
						})}
					</div>
				</div>
			</div>
		)
	}
}

export default Info;
